import React from 'react'
import {Container,Title} from '../common'
import {injectIntl,FormattedMessage} from 'gatsby-plugin-intl'
import config from '../../../config/default.json'
const BonusItems = config.bonusItems;

const BlockBonusItem = ({l,i,r,c,ch,p}) => {
    return (
        <div className="w-full md:w-1/5 md:px-1 mb-8 md:mb-0" data-aos="flip-left" data-aos-offset="100" data-aos-duration="600">
            <div className={`text-white text-3xl uppercase font-nexa text-center px-8 py-8 bg-${c}`}>
                <div className="mb-0 leading-none">
                    <FormattedMessage id="index.bonus.rank" defaultMessage="Rank"/>
                </div>
                <div className="leading-none">{l}</div>
            </div>
            <div className="bg-white px-8 py-8 text-center md:px-4 md:py-4 lg:px-8 lg:py-8">
                    <div className="font-sans font-semibold text-18 text-lights-gray">
                        <FormattedMessage id="index.bonus.num" defaultMessage="ID Number" />
                    </div>
                <div className="font-sans font-semibold text-24 text-lights-bluebold mb-8 md:mb-4 lg:mb-8">{i}</div>
                <div className="inline-block text-center relative">
                    <svg className="w-24 h-24 mx-auto"viewBox="0 0 400 400" style={{transform: 'rotate(-90deg)'}}>
                        <circle cx="200" cy="200" r="180" fill="none" strokeWidth="20" stroke="#e1e1e1"></circle>
                        <circle cx="200" cy="200" r="180" fill="none" strokeWidth="20" stroke={`${ch}`} strokeDasharray={Math.PI*180*2} strokeDashoffset={((100-p)/100)*(Math.PI*180*2)} strokeLinecap="round"></circle>
                    </svg>
                    <div className="absolute bonus-circle-text">
                        <div className="font-sans font-semibold text-18 text-lights-gray">
                            <FormattedMessage id="index.bonus.award" defaultMessage="Award" />
                        </div>
                        <div className={`font-sans font-extrabold text-24 leading-none text-${c}`}>{r}</div>
                    </div>
                </div>
            </div>
        </div>        
    )
}

const BlockBonus = () => {
    return (
        <Container classNames="bg-lights-lightsemibold" ids="bonus">
            <Title>
                <FormattedMessage
                    id="index.bonus.title1"
                    defaultMessage="Bo"
                />
                <span className="text-lights-orangebold"><FormattedMessage
                    id="index.bonus.title2"
                    defaultMessage="Nus"
                /></span>
            </Title>
            <div className="flex flex-wrap md:-mx-1">
                {BonusItems.map((val,key)=>{
                    return <BlockBonusItem key={key} l={key+1} i={val[0]} r={val[1]} c={val[2]} ch={val[3]} p={val[4]}/>
                })}                
            </div>
        </Container>
    )
}

export default injectIntl(BlockBonus);