import React from 'react'
import {injectIntl,FormattedMessage,Link} from 'gatsby-plugin-intl'
import config from '../../../config/default.json';
const HeaderItems = config.headerItems;
const BlockItem = ({path,title,desc}) => {
    return (
        <div className="w-full md:w-1/2 lg:w-1/4 px-3 lg:px-4 xl:px-8 mb-8 lg:mb-0 header-box-item" data-aos="fade-up">
            <div className="block-header-item text-center">
                <img src={path} alt="" className="mx-auto max-w-full mb-2"/>
                <p className="font-sans font-semibold text-white text-18">{title}</p>
                <p className="font-sans text-black text-18">{desc}</p>
            </div>
        </div>
    )
}

const BlockHeader = ({intl}) =>{
    return (
        <div className="block-header">
            <div className="container mx-auto px-2 xl:px-4 overflow-hidden">
                <div className="header--title" data-aos="fade-left">
                    Crypto<span className="text-lights-bluebold">lights</span>
                </div>
                <div className="header--desc" data-aos="fade-right">
                    <FormattedMessage id="index.header.decs" defaultMessage="For you and everyone sustainable make money" />
                </div>
                <div className="flex lg:-mx-4 xl:-mx-8 py-12 flex-wrap">
                    {HeaderItems.map((val)=>{
                        return (
                            <BlockItem
                                key={val.titleId}
                                path={val.path}
                                title={intl.formatMessage({id: val.titleId,defaultMessage: val.title})}
                                desc={intl.formatMessage({id: val.descId,defaultMessage: val.desc})}
                            />
                        )
                    })}
                </div>
                <div className="text-center">
                    <Link to="/signup/" className="inline-block bg-lights-blue px-8 py-6 rounded-lg text-center mx-auto">
                        <p className="text-lights-gray font-sans header-button-title mb-2 font-semibold">                         <FormattedMessage id="index.header.button.title-1" defaultMessage="1,000 ETH for YOU"/>
                        </p>
                        <p className="text-lights-orangebold font-sans header-button-title-2 uppercase font-extrabold">
                            <FormattedMessage id="index.header.button.title-2" defaultMessage="GET it, NOW"/>
                        </p>
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default injectIntl(BlockHeader);