import React,{useEffect} from "react"
import {injectIntl} from 'gatsby-plugin-intl'
import queryString from 'query-string'
import Cookies from 'js-cookie'
import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
//index component
import BlockHeader from '../components/index/blockheader'
import BlockInfo from '../components/index/blockinfo'
import BlockDetail from '../components/index/blockdetail'
import BlockHow from '../components/index/blockhow'
import BlockProfit from '../components/index/blockprofit'
import BlockBonus from '../components/index/blockbonus'
import BlockAd from '../components/index/blockad'
const IndexPage = (props) => {
  useEffect(()=>{
    const queryParams = queryString.parse(props.location.search);
    if (queryParams.ref){
      Cookies.set('ref',queryParams.ref)
    }
  },[null])
  return (
    <Layout>
      <SEO title="Home" />
      <BlockHeader />
      <BlockInfo />
      <BlockDetail />
      <BlockHow />
      <BlockProfit />
      <BlockBonus />
      <BlockAd />
    </Layout>
  )
}

export default injectIntl(IndexPage)
