import React from 'react'
import {injectIntl,FormattedMessage,Link} from 'gatsby-plugin-intl'
import config from '../../../config/default.json'
import {Container,Title} from '../common'
const HowItems = config.howItems;
const BlockHowItem = ({path,title,desc,bg,border,classBox,none}) => {
    return (
        <div className={`w-full md:w-1/2 md:px-4 lg:w-1/5 ${classBox}`} data-aos="fade-up" data-aos-offset="-100">
            <div className="how-icon">
                <div className={`w-full h-full flex items-center justify-center rounded-full ${bg}`}>
                    <img src={path} alt="" className="mx-auto"/>
                </div>
            </div>
            <div className={`w-full how-item h-48 py-3 px-4 relative ${border}`}>  
                {none?null:<div className={`how-sym ${border}`}></div>}
                <div className="text-lights-bluebold font-sans font-bold text-16 uppercase mb-2">{title}</div>
                <div className="font-sans text-lights-graylight text-14">{desc}</div>
            </div>
        </div>
    )
}

const BlockHow = ({intl}) => {
    return (
        <Container classNames="bg-lights-semibold">
            <Title>
                <FormattedMessage
                    id="index.how.title1"
                    defaultMessage="How It "
                />
                <span className="text-lights-orangebold"><FormattedMessage
                    id="index.how.title2"
                    defaultMessage="Works"
                /></span>
            </Title>
            <div className="flex flex-wrap md:-mx-4 how-container mb-4">
                {HowItems.map((val,key)=>{
                    return <BlockHowItem key={key} title={intl.formatMessage({id: `${val.titleId}`,defaultMessage: val.title})} desc={intl.formatMessage({id: val.descId,defaultMessage: val.desc})} path={val.path} bg={val.bg} border={val.border} classBox={val.class} none={val.none}/>
                })}
            </div>
            <div className="text-center">
                <Link to="/howitworks" className="inline-block px-6 py-1 text-14 font-sans font-semibold text-lights-bluebold border rounded-lg border-lights-orangesemilight mx-auto hover:bg-lights-orangesemilight hover:text-white"><FormattedMessage id="index.how.btn" defaultMessage="See More"/></Link>
            </div>
        </Container>
    )    
}

export default injectIntl(BlockHow)